<template>
    <div id='signup'>
        <h1>
            
        </h1>
        <input v-model="user" placeholder=""></input>
        <input v-model="passwd" placeholder=""></input>
        <input v-model="nickname" placeholder=""></input>
        <input v-model="email" placeholder=""></input>
        <button @click="tosignup"></button>
        <button @click="back"></button>
    </div>
</template>

<script>
import { request } from '../js/request.js';
export default {
    data(){
        return{}
    },
    methods: {
        async tosignup(){
            //
            var data = {
                "data": {
                    "user": this.user,
                    "passwd": this.passwd,
                    "nickname": this.nickname,
                    "email": this.email
                }
            };
            try {
                let res = await request('/signup', 'post', data);//
                window.alert(res.msg);
                this.$router.push('/login');//
            } catch(error) {
                window.alert(error);
            }
        },
        back(){
            this.$router.push("/");
        }
    }
}
</script>


